.metricContainer {
  padding-top: 100px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.formMetric {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto auto;
  gap: 10px;
  border: 1px solid #ccc;
  border-radius: 10px;
  padding: 20px;
  @media (max-width: 480px) {
    gap: 16px;
  }
}

.ant-picker  {
  width: 165px;
}

.buttonMetric {
  grid-column: span 2;
}

@media (hover: hover) {
  .buttonMetric:where(.css-dev-only-do-not-override-cdzvx5).ant-btn-variant-solid:not(:disabled):not(.ant-btn-disabled):hover {
    background: #4096ff;
    color: #fff;
  }
}

@media (hover: none) {
  .buttonMetric:where(.css-dev-only-do-not-override-cdzvx5).ant-btn-variant-solid:not(:disabled):not(.ant-btn-disabled):hover {
    background:#1677ff;
    color: #fff;
  }
    .buttonMetric:where(.css-dev-only-do-not-override-cdzvx5).ant-btn-variant-solid:not(:disabled):not(.ant-btn-disabled):active {
    background: #4096ff;
    color: #fff;
  }
}

.multiSelect {
  grid-column: span 2;
}

.errorMessage {
  color: red;
  text-align: left;
  grid-column: span 2;
}

.icon {
  width: 25px;
}

.ant-picker-input input,
.ant-select-selection-search-input,
.ant-input {
  font-size: 16px !important;
}


html {
  touch-action: manipulation;
}
